@use "setting" as *;

.body-header-active {
    .footer-fixed-line {
        opacity: 1;
        transform: translateY(0px);
    }
}


.footer {
    background-color: #FEFBF5;
    position: relative;
    padding: 75px 0px 20px;
    z-index: 4;

    @include media(middle) {
        //padding: 45px 0px 80px;
    }

    .content-width {
        @include media(lg) {
            width: 90%;
        }

        @include media(middle) {
            width: 85%;
        }
    }


    .footer-wrap {
        margin-bottom: 30px;

        .footer-wrap-menu {
            display: flex;
            flex-wrap: wrap;
            //justify-content: space-between;
            gap: 45px;
            margin-bottom: 48px;

            dl {
                dt {
                    margin-bottom: 20px;

                    &:last-child {
                        margin-bottom: 0;
                    }

                    a {
                        font-size: 16px;
                        font-weight: bold;
                        letter-spacing: .1em;
                    }
                }

                dd {
                    margin-bottom: 10px;

                    &:last-child {
                        margin-bottom: 0;
                    }

                    a {
                        font-size: 16px;
                        font-weight: 500;
                        letter-spacing: .1em;
                    }
                }
            }
        }

        .footer-banner {
            display: flex;
            flex-wrap: wrap;
            gap: 15px;
            margin-bottom: 25px;

            li {
                width: 23.5%;
            }
        }

        .footer-info {
            display: flex;
            flex-wrap: wrap;
            align-items: center;

            gap: 15px;

            .footer-logo {
                width: 203px;

                img {
                    width: 100%;
                }
            }

            .footer-access {
                p {
                    font-size: 14px;
                    font-weight: 500;
                    letter-spacing: .1em;
                    line-height: 1.35;
                    margin-bottom: 0;
                }
            }

            .footer-info-wrap {
                display: flex;
                flex-wrap: wrap;
                align-items: center;

                dl {
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;
                    gap: 13px;

                    dt {
                        display: inline-flex;
                        align-items: center;
                        justify-content: center;
                        height: 27px;
                        background-color: #3C3636;
                        padding: 0 13px;
                        font-size: 14px;
                        font-weight: 600;
                        letter-spacing: .1em;
                        color: #fff;
                        border-radius: 14px;
                    }

                    dd {
                        font-size: 14px;
                        font-weight: 500;
                        letter-spacing: .1em;
                    }
                }

                .footer-info-wrap-time {
                    margin-right: 20px;
                }
            }
        }
    }





    .footer-fixed {
        position: fixed;
        right: 20px;
        bottom: 20px;
        z-index: 5;

        a {
            display: block;
            font-size: 14px;
            color: #fff;
            line-height: 1.4;
            text-align: center;
            width: 132px;
            height: 134px;
            position: relative;

            &:hover {
                .footer-fixed-child-1 {
                    img {
                        transform: translate(0, -3px) rotate(360deg) scale(1.2);
                    }
                }

                .footer-fixed-child-2 {
                    img {
                        transform: translate(2px, -3px) rotate(360deg) scale(1.2);
                    }
                }

                .footer-fixed-child-3 {
                    img {
                        transform: translate(4px, -3px) rotate(360deg) scale(1.2);
                    }
                }

                .footer-fixed-child-4 {
                    img {
                        transform: translate(6px, -3px) rotate(360deg) scale(1.2);
                    }
                }

                .footer-fixed-child-5 {
                    img {
                        transform: translate(7px, -3px) rotate(360deg) scale(1.2);
                    }
                }

                .footer-fixed-child-6 {
                    img {
                        transform: translate(8px, 0px) rotate(360deg) scale(1.2);
                    }
                }

                .footer-fixed-txt {
                    img {
                        transform: scale(1.1);
                    }

                }
            }

            span {
                position: absolute;
                display: block;

                &:first-of-type {
                    width: 119.47px;
                    height: 119.47px;
                    left: 0;
                    bottom: 0;
                }

                &:nth-of-type(2) {
                    width: 10.39px;
                    height: 11.1px;
                    top: -13px;
                    left: 45px;
                }

                &:nth-of-type(3) {
                    width: 9.29px;
                    height: 13.47px;
                    top: -14px;
                    left: 61px;
                }

                &:nth-of-type(4) {
                    width: 10.17px;
                    height: 13.47px;
                    top: -11px;
                    left: 78px;
                }

                &:nth-of-type(5) {
                    width: 8.79px;
                    height: 13.25px;
                    top: -2px;
                    left: 93px;
                }

                &:nth-of-type(6) {
                    width: 7.73px;
                    height: 11.44px;
                    top: 9px;
                    left: 105px;
                }

                &:nth-of-type(7) {
                    width: 7.08px;
                    height: 10.24px;
                    top: 23px;
                    left: 112px;
                }

                &:nth-of-type(7) {
                    width: 7.08px;
                    height: 10.24px;
                    top: 23px;
                    left: 112px;
                }

                &:last-of-type {
                    width: 52px;
                    height: 70.46px;
                    left: 35px;
                    bottom: 25px;
                }

                img {
                    width: 100%;
                    height: 100%;
                    transition: all 0.4s ease;
                }
            }

            .footer-fixed-txt {
                overflow: hidden;
            }
        }
    }

    .footer-copy {
        width: 90%;
        margin: 0 auto;

        .footer-copy-link {
            margin-bottom: 10px;

            ul {
                display: flex;
                justify-content: center;

                li {
                    padding: 0 10px;

                    &:last-of-type {
                        a {
                            &::before {
                                display: none;
                            }
                        }
                    }

                    a {
                        font-size: 12px;
                        font-weight: 500;
                        letter-spacing: .1em;
                        position: relative;

                        &::before {
                            position: absolute;
                            display: block;
                            content: "";
                            background-color: #3C3636;
                            height: 12px;
                            width: 1px;
                            right: -10px;
                            top: 50%;
                            transform: translateY(-50%);
                        }
                    }
                }
            }
        }
    }

    .copy {
        text-align: center;
        font-size: 12px;
        font-weight: 500;
        margin: 0px;
        letter-spacing: .1em;
    }


}

.scroll-txt-change {
    position: fixed;
    left: 12px;
    top: 150px;
    margin-bottom: 0;

    span {
        display: block;
        width: 37px;
        height: 440px;


        img {
            width: 100%;
            height: 100%;
        }
    }

    .sub-title-01,
    .sub-title-02 {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        transition: opacity 0.4s ease;
    }

    .sub-title-02 {
        opacity: 1; // 下層ではこれがデフォルト
    }

    &.show-title-01 .sub-title-01 {
        opacity: 1;
        z-index: 1;
    }

    &.show-title-01 .sub-title-02 {
        opacity: 0;
        z-index: 0;
    }
}